input{
color:white;
padding: 0.6rem;
border-radius:25px;
border: 1px solid;
border-color: white;
background: transparent;
margin-top: 1rem;
margin-right: 1rem;
}

::placeholder{
  color: white;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.container {
  width: 50%;
  height: auto;
  min-height: 25rem;
  max-width: 500px;
  min-width: 25rem;
  background: url("./assets/mountain.jpg") no-repeat center center/cover;
  background-size: 100% 100%;
  border-radius: 20px;
  box-shadow: 4px 3px 7px 2px #00000040;
  padding: 1rem;
  box-sizing: border-box;
}

.top {
  width:100%;
  height:100%;
  margin-bottom:2rem;
}

.description{
  margin-top: 0;
  font-size:0.5rem;
}

.bot{
  display: flex;
  flex-flow:row wrap;
  justify-content: space-evenly;
  -webkit-box-align: center;
  align-items: center;

}

.weather-info{
  border-radius:25px;
  border: 1px solid; 
  background-color:honeydew;
  width:30%;
  margin:auto;
}

.icon-properties{
  /* position:relative;
  top: 20px;
  font-size: 2rem; */
  width:36px;
  height:36px;
}

.align-left{
  text-align: left;
}

.sunset{
  display: flex;
  margin: 5px 10px;
  flex-direction: row;
  justify-content: space-evenly;
  -webkit-box-align: center;
  align-items: center;
}

.icon-description{
  display: flex;
  flex-direction: column;
  font-size: 20px;
  margin: 15px;
  text-align:left;
}